import Vue from 'vue'
import Router from 'vue-router'
import PersonHFive from 'components/personHFive/personHFive'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'personHFive',
      component: PersonHFive
    },
    {
      path: '/personHFive',
      name: 'personHFive',
      component: PersonHFive
    }
  ]
})



