<template>
  <div class="personHFive" ref="hv" v-wechat-title="$route.meta.title = task.taskTheme" :style="`background-color: ${clientHeight}`">
    <div class="perBg">
      <div class="qr-details">
        <div class="title">
          <img v-if="getPersonalInfo.headPortraitUrl === null" src="../../assets/beauGirl.jpg" />
          <img v-else :src="getPersonalInfo.headPortraitUrl" />
        </div>
        <div class="addQrcode">
          <span style="color: #ff9100;font-weight: 700;font-size: 0.3rem;">{{getPersonalInfo.nickname}}</span>
          <span style="font-size: 0.3rem;">{{task.recommendedReasonsList[0].content}}</span>
          <img :src="getPersonalInfo.qrCode" />
        </div>
        <div class="title t1"><i class="el-icon-success" style="padding-right: 0.1rem;"></i>此二维码已经过安全认证，请放心扫描</div>
      </div>
    </div>
  </div>
</template>

<script>
// import {apiPages, ERR_OK, https} from 'api/config'
import {getCookie} from '../../api/cookie'
export default {
  name: 'personHFive',
  data () {
    return {
      pname: '',
      clientHeight: '',
      getPersonalInfo: [], // 个人号信息
      task: [] // 任务信息
    }
  },
