    let Base64 = require('js-base64').Base64
    // this.getCookiesInfo = JSON.parse(cookies)
    let getCookiesInfoList = Base64.decode(cookies)
    console.log(getCookiesInfoList)
    this.getPersonalInfo = JSON.parse(getCookiesInfoList).no
    this.task = JSON.parse(getCookiesInfoList).task
    console.log(this.getPersonalInfo)
    // 获取浏览器地址的id
