    // var url = 'www.baidu.com?id=65#/' // 获取url中"?"符后的字串
    // var theRequest = [] // 定义一个空的数组，用来存储截取的字符串
    // if (url.indexOf('?') !== -1) {
    //   var str = url.substr(1)
    //   var strs = str.split('&')
    //   for (var i = 0; i < strs.length; i++) {
    //     theRequest[strs[i].split('=')[0].slice(-2)] = unescape(strs[i].split('=')[1])
    //   }
    // }
    // console.log(theRequest)
    // var lin = theRequest.id
    // this.taskId = lin.split('#')[0]
    // console.log('我是在浏览器地址中截取的任务ID：' + this.taskId)
    // this.$http.get(`${apiPages}/personal/noTask/getPersonalByTaskId/${this.taskId}/`).then((response) => {
    //   console.log(response)
    //   if (response.body.code === ERR_OK) {
    //     this.$message({
    //       type: 'error',
    //       message: '页面信息走丢了，请重试！！'
    //     })
    //   } else {
    //     this.data = response.body.data
    //     this.https = https
    //     this.pname = response.body.data.task.taskTheme
    //     this.resonse = response.body.data.task.recommendedReason
    //   }
    // })
  },
  methods: {
    // 动态修改样式
    changeFixed (clientHeight) {
      console.log(clientHeight)
      this.$refs.hv.style.height = clientHeight + 'px'
    }
  },
  watch: {
    // 如果 `clientHeight` 发生改变，这个函数就会运行
    clientHeight: function () {
      this.changeFixed(this.clientHeight)
    }
  }
}
</script>
<style scoped>
  .personHFive {
    width: 100%;
    position: relative;
    background: #f9dd6f;
    /*overflow-x: hidden;*/
    /*overflow-y: auto;*/
    overflow: hidden;
  }
  .personHFive .perBg {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
  }
  .personHFive .qr-details {
    width: 6rem;
    background: #fff;
    height: 7rem;
    margin: 0 auto;
    margin-top: 2rem;
    position: relative;
    border-radius: 0.2rem;
  }
  .personHFive .avatar img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
  .personHFive .title {
    width: 100%;
    text-align: center;
    font-size: 0.3rem;
    margin-top: 0.5rem;
  }
  .personHFive .t1 {
    height: 0.6rem;
    line-height: 0.6rem;
    margin-top: 0rem;
    font-size: 0.25rem;
    background: #f0feef;
    color: #64bf56;
  }
  .personHFive .addQrcode {
    width: 80%;
    margin: 0 auto;
    padding: 0rem 0.5rem;
    box-sizing: border-box;
    margin-top: 1.2rem;
  }
  .personHFive .addQrcode span {
    display: block;
    text-align: center;
    /*font-size: 1rem;*/
  }
  .personHFive .addQrcode img {
    width: 100%;
  }
  .personHFive .bring-title img{
    width: 0.3rem;
    height: 0.3rem;
  }
  .personHFive .bring-ul2 li {
    float: left;
    font-size: 0.2rem;
    text-align: center;
    padding-right: 0.2rem;
    list-style: none;
  }
  .personHFive .bring-ul2 li:last-child {
    float: right;
  }
  .personHFive .bring-ul2 li:nth-child(1) {
    width: 1.5rem;
    text-align: left;
  }
  .personHFive .bring-ul2 li:nth-child(2) {
    width: 3rem;
    text-align: left;
  }
  .personHFive .details img {
    width: 100%;
  }
  .personHFive .title img {
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 50%;
    position: absolute;
    top: -0.5rem;
    left: 50%;
    margin-left: -0.75rem;
  }
</style>



